export default {
  name: 'ordgood',
  props: {
    item: {
      type: Object,
      default: null
    }
  },
  data() {
  	return {
  		  platform: 0
  	};
  },
  mounted() {
  	// 设备样式兼容
  	this.platform = uni.getStorageSync('platform');
	// console.log('platform:',this.platform)
  },
  methods:{
	  goDetail(){
		  uni.navigateTo({
		  	url:'/pages/order/goodsDetail/goodsDetail?goodsId='+this.item.goodsId + '&shopId='+this.item.shopId
		  })
	  }
  }
}
